body {
  font-family: 'Roboto Slab', sans-serif;
  color: #eee;
  background: #2C1617;
  background-image: url(../images/paintings_june_exposition/cuadro_plaza_ramales.jpg);
  background-image: url(../images/paintings_june_exposition/cuadro_plaza_ramales.jpg), linear-gradient(-180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.35) 50%);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.page__main__section {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 5%;
}

.page__main__section-title {
  width: 100%;
  margin-bottom: 15px;
  font-size: 31px;
  text-transform: uppercase;
}

@media all and (min-width: 768px) {
  .page__main__section-title {
    font: Arial;
  }
}
